* {
  margin: 0;
  padding: 0;
}
body {
  background-color: #fafafa;
}
.icon-menu {
  display: flex;
  justify-content: space-between;
  margin: 1em 0;

  .interactions {
    display: flex;
    padding-left: 1em;
  }

  .icon {
    margin-right: 1em;
    width: 23px;
    height: 23px;
    &:hover {
      cursor: pointer;
    }
  }
}
.center {
  width: 75%;
  height: 100%;
  margin: 20px auto;
  border: 1px solid rgb(202, 202, 202);
}

.navbar {
  width: 100%;
  height: 20%;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: 1px solid rgb(202, 202, 202);
}
.camera {
  margin: 0 70% 0 0%;
}
.insta {
  margin: 0 25% 0 0;
}
.send {
  width: 50%;
  margin: 1% 0 0 0;
}

.profile {
  background: white;
  height: 600px;
  display: flex;
  flex-direction: column;
  overflow: auto;
  
input{
  margin-left: 3px;
  width: 80%;
  height: 30px;
  border: 1px solid rgb(202, 202, 202);
}
button{
  padding-left: 10px;
  height: 30px;
  color: #bec1c3;
  &:hover{
    cursor: pointer;
    color: black;
  }

}
}
.photo-info {
  padding: 10px 10px;
  font-size: 12px;
  line-height: 1.5em;

  img {
    padding-right: 10px;
  }
}

.photo-info span {
  font-weight: 600;
}

.profile .post {
  height: 100%;
  width: 100%;
}

.val {
  width: 100%;
  height: 25%;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.heart {
  content: url("https://icons-for-free.com/free-icons/png/512/1904657.png");
  background-repeat: no-repeat;
  width: 4%;
  margin: 1% 0% 1% 3%;
  cursor: pointer;
}

.heart:hover {
  content: url("https://upload.wikimedia.org/wikipedia/commons/thumb/f/f1/Heart_coraz%C3%B3n.svg/1200px-Heart_coraz%C3%B3n.svg.png");
}

.comment {
  content: url("https://cdn3.iconfinder.com/data/icons/eldorado-stroke-basic/40/comment_baloon-512.png");
  width: 4%;
  margin: 0.5% 0 0 3%;
  cursor: pointer;
}
.air {
  content: url("data:image/png;base64,");
  width: 7%;
  margin: 0% 0 0 2%;
  cursor: pointer;
}
.save {
  content: url("https://cdn2.iconfinder.com/data/icons/instagram-ui/48/jee-67-512.png");
  width: 9%;
  margin: 0 0 0 67%;
  cursor: pointer;
}
.save:hover {
  content: url("data:image/png;base64,");
  cursor: pointer;
}
.words {
  width: 100%;
  height: 20%;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
button {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}
p {
  color: black;
  font-size: 100%;
  font-family: Helvetica;
}
.liked {
  margin-left: 5%;

  height: 10%;
  width: 100%;
}
.by {
  margin: 0 0 0 0;

  width: 100%;
  margin-top: 10%;
}
br {
  display: block;

  line-height: 25px;
}
.grey {
  color: #bec1c3;
  font-size: 100%;
  font-family: Helvetica;
}

.space {
  line-height: 0%;
  display: block;
}
.navbar2 {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  background-color: white;
  border-top: solid #bec1c3 2px;
}

.bar {
  content: url("https://i2.wp.com/coleandtaffy.com/wp-content/uploads/2016/07/IG-bottom.jpg");
  width: 90%;
  cursor: pointer;
}
